import { Component, OnInit } from '@angular/core';
import { Search } from './search';
import { FindPlaceService } from './find-place.service';
import { FormControl } from '@angular/forms';


@Component({
  selector: 'app-find-form',
  templateUrl: './find-form.component.html',
  styleUrls: ['./find-form.component.css'],
})
export class FindFormComponent implements OnInit {
  date = new FormControl(new Date());
 
  destination = ['Srilanka', 'Italy', 'USA']

  guests = [1, 2, 3]
  
  curday = function(sp){
   var today = new Date();
   var dd:any = today.getDate();
   var mm:any = today.getMonth()+1; //As January is 0.
   var yyyy = today.getFullYear();
   
   if(dd<10) dd='0'+dd;
   if(mm<10) mm='0'+mm;
   return (mm+sp+dd+sp+yyyy);
   };

 searchModel = new Search('Srilanka', this.date, this.date , 2)

 constructor(private _enrollmentService: FindPlaceService) { }

 onSubmit () {
   console.log(this.searchModel);
   this._enrollmentService.enroll(this.searchModel)
   .subscribe(
     data => console.log('Success', data),
     error => console.log('error !', error)
   )
 }

 ngOnInit() {
 }

}
